import { defineComponent, ref } from 'vue';
import Ecahrts from './components/ecahrts.vue';
import Table from './components/Table.vue';
import { exportWord } from './utils';
export default defineComponent({
  name: '',
  components: {
    Ecahrts,
    Table
  },

  setup() {
    const echarts = ref();
    const table = ref();

    const wordBtn = () => {
      const paragraph = [...window.document.querySelectorAll('.content')].map(item => {
        return item.innerText;
      });
      let img = echarts.value.echartInstance().getDataURL({
        pixelRatio: 2,
        backgroundColor: '#fff'
      });
      let contentWord = {
        p0: paragraph[0],
        p1: paragraph[1],
        p2: paragraph[2],
        p3: paragraph[3],
        img,
        dataTble: table.value.dataTable
      };
      exportWord('word', contentWord);
    };

    return {
      wordBtn,
      echarts,
      table
    };
  }

});