import Docxtemplater from "docxtemplater";
import PizZip from "pizzip";
import JSZipUtils from "jszip-utils";
import { saveAs } from "file-saver";
/**
     * description echarts
     */
export function base64DataURLToArrayBuffer(dataURL: string) {
  const base64Regex = /^data:image\/(png|jpg|svg|svg\+xml);base64,/;
  if (!base64Regex.test(dataURL)) {
    return false;
  }
  const stringBase64 = dataURL.replace(base64Regex, "");
  const binaryString = window.atob(stringBase64);
  const len = binaryString.length;
  const bytes = new Uint8Array(len);
  for (let i = 0; i < len; i++) {
    const ascii = binaryString.charCodeAt(i);
    bytes[i] = ascii;
  }
  return bytes.buffer;
}

export function exportWord(title: string, contentWord: object) {
  const ImageModule = require("open-docxtemplater-image-module");
  JSZipUtils.getBinaryContent(
    "word.docx",
    function (error: any, content: any) {
      // input.docx
      // 
      if (error) {
        throw error;
      }
      // 
      const opts: any = {};
      opts.centered = false;
      opts.getImage = (chartId: string) => {
        return base64DataURLToArrayBuffer(chartId);
      };
      opts.getSize = function () {
        //
        return [600, 290];
      };

      const imageModule = new ImageModule(opts);
      // PizZip
      const zip = new PizZip(content);
      // docxtemplater
      const doc = new Docxtemplater();
      opts.centered = true; // word{%image}
      opts.fileType = "docx";
      doc.attachModule(imageModule);
      doc.loadZip(zip);
      // 
      doc.setData(contentWord);
      try {
        // 
        doc.render();
      } catch (error: any) {
        // 
        const e = {
          message: error.message,
          name: error.name,
          stack: error.stack,
          properties: error.properties,
        };
        console.log(
          JSON.stringify({
            error: e,
          })
        );
        throw error;
      }
      // docxtemplaterzip
      const out = doc.getZip().generate({
        type: "blob",
        mimeType:
          "application/vnd.openxmlformats-officedocument.wordprocessingml.document",
      });
      // 
      saveAs(out, title + ".docx");
    }
  );
}